@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;600&display=swap');

body,
html {
  height: 100%;
  margin: 0;
  background-color: #0a043c;
  color: #fff;
}

.app-container {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 200px;
  background-color: #341968;
  padding: 20px;
  color: #fff;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar li a {
  color: #fff;
  text-decoration: none;
  display: block;
  padding: 10px 0;
}

.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.content header {
  margin-bottom: 20px;
}

/* Стили кнопок переключения (Analog/Digital) */
.toggle-buttons {
  display: flex;
  justify-content: center; /* Центрировать кнопку */
  margin-bottom: 20px; /* Отступ снизу от кнопки */
}

.toggle-buttons button {
  margin: 5px;
  padding: 16px 24px; /* было 8px 12px */
  cursor: pointer;
  background-color: #5a3a99;
  color: white;
  border: none;
  border-radius: 5px;
  align-self: center; /* Центрировать кнопку */
  font-family: 'Raleway', sans-serif;
  font-size: 18px; /* было не указано, поставил увеличенный */
}

/* Изменения для центрирования блока спидометра */
.speedometer-section {
  display: flex;
  flex-direction: column;
  align-items: center; /* Центрировать по горизонтали */
  justify-content: center; /* Центрировать по вертикали */
  width: 100%;
  height: 100%;
  gap: 40px;
}

.speedometer-container {
  display: flex;
  flex-direction: column; /* Размещаем спидометр и кнопки единиц измерения вертикально */
  align-items: center; /* Центрировать по горизонтали */
  gap: 20px;
}

/* Контейнер для спидометра и кнопок единиц измерения */
.speedometer-unit-wrapper {
  display: flex;
  align-items: center; /* Центрировать по вертикали */
  gap: 60px;
}

/* Стили дисплея спидометра */
.speedometer-display {
  width: 400px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  overflow: hidden;
  background-color: #341968;
  color: #fff;
  font-size: 48px;
}

.speedometer-display img {
  max-width: 100%;
  max-height: 100%; 
  object-fit: contain; 
}

.digital-speedometer p {
  font-family: 'Raleway', sans-serif;
  font-size: 40px;
}

.unit-selector-container {
  display: flex;
  justify-content: center; 
}

.unit-selector {
  display: flex;
  flex-direction: column; 
  gap: 5px; 
}

.unit-selector button {
  margin: 0;
  padding: 16px 24px; 
  cursor: pointer;
  background-color: #5a3a99;
  color: white;
  border: none;
  border-radius: 5px;
  font-family: 'Raleway', sans-serif;
  font-size: 32px; 
}

.speed-controls {
  display: flex;
  flex-direction: column; 
  align-items: center; 
  gap: 10px;
  margin-top: 30px; 
  font-family: 'Raleway', sans-serif;
}

.speed-control-buttons {
  display: flex;
  gap: 10px;
}

.speed-control-buttons button {
  padding: 16px 24px; 
  cursor: pointer;
  background-color: #5a3a99;
  color: white;
  border: none;
  border-radius: 5px;
  font-family: 'Raleway', sans-serif;
  font-size: 32px; 
}

.start-button {
  padding: 20px 40px; 
  font-family: 'Raleway', sans-serif;
  font-size: 32px;
  background-color: #5a3a99;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
